import React from "react";
import { Routes, Route } from "react-router-dom";
import { useSelector } from "react-redux";
import Login from "./Login";
import LoginError from "./LoginError";
import Logout from "../Login/Logout";
import MyPage from "../MyPage/MyPage";
import Signup from "../Signup/Signup";
import FindPassword from "./FindPassword";
import FindEmail from "./FindEmail";
import ChangePassword from "../MyPage/ChangePassword";
import GetMyPosts from "../MyPage/GetMyPosts";
import Redirect from "./Redirect";

interface IsTokenProps {
  userNickName: string;
}

const AuthRouter: React.FC<IsTokenProps> = ({ userNickName }) => {
  const isToken = useSelector((state: any) => state.auth.isAuthenticated);

  return (
    <Routes>
      {isToken ? (
        <>
          <Route path="/myposts" element={<GetMyPosts />} />
          <Route
            path="/find-email"
            element={
              <LoginError pageName={"FindEmail"} error={" !"} />
            }
          />
          <Route
            path="/login"
            element={
              <LoginError pageName={"Login"} error={" !"} />
            }
          />
          <Route
            path="/signup"
            element={
              <LoginError
                pageName={"Signup"}
                error={"    !"}
              />
            }
          />
          <Route path="/change-password" element={<ChangePassword />} />
          <Route path="/findpassword" element={<FindPassword />} />

          <Route path="/logout" element={<Logout nickName={userNickName} />} />
          <Route path="/mypage/:pagenumber" element={<MyPage />} />
        </>
      ) : (
        <>
          <Route
            path="/myposts"
            element={
              <LoginError
                pageName={"MyPosts"}
                error={"  !"}
              />
            }
          />
          <Route
            path="/logout"
            element={
              <LoginError
                pageName={"Logout"}
                error={"  !"}
              />
            }
          />
          <Route
            path={"/mypage/*"}
            element={
              <LoginError pageName={"MyPage"} error={"  !"} />
            }
          />
          <Route
            path="/change-password"
            element={
              <LoginError
                pageName={"ChangePassword"}
                error={" !"}
              />
            }
          />
          <Route path="/find-email" element={<FindEmail />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/redirect" element={<Redirect />} />
        </>
      )}
    </Routes>
  );
};

export default AuthRouter;
