import React, { useState, useLayoutEffect } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import backgroundImage from "../../shared/images/LoginPage/logout-background.jpg";
import axios from "axios";
import { DEEP_BROWN } from "../../styles/colors";
import { useDispatch } from "react-redux";
import { logout } from "../../store/slices/authSlices";

interface LogoutProps {
  nickName: string;
}

const Logout: React.FC<LogoutProps> = ({ nickName }) => {
  const navigator = useNavigate();
  const [userToken, setUserToken] = useState<string>("");

  const dispatch = useDispatch();

  useLayoutEffect(() => {
    if (nickName === "") {
      setUserToken("");
    } else {
      setUserToken(nickName);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const logoutHandler = async () => {
    const token = localStorage.getItem("token");

    if (token !== null) {
      await axios
        .post("https://www.onesol.shop/auth/logout", null, {
          headers: {
            Token: token,
          },
        })
        .then((res) => {
          // 
          localStorage.removeItem("nickName");
          localStorage.removeItem("token");
          localStorage.removeItem("userId");
          dispatch(logout());
          console.log(res);

          navigator("/");
        })
        .catch((err) => {
          //  err 
          localStorage.removeItem("nickName");
          localStorage.removeItem("token");
          localStorage.removeItem("userId");
          dispatch(logout());

          console.log(err);

          //alert(err.message);
          navigator("/");
        });
    }
  };

  const onClickHandler = () => {
    navigator("/");
  };

  if (userToken) {
    return (
      <>
        <UserLogout>
          <BackgroundImgLogout>
            <ImgLogout src={backgroundImage} alt="" />
            <MessageLogout>
              {userToken && (
                <>
                  <b>{userToken} </b>  ?
                </>
              )}
              <ButtonLogout onClick={logoutHandler}> </ButtonLogout>
            </MessageLogout>
          </BackgroundImgLogout>
        </UserLogout>
      </>
    );
  } else if (userToken === "") {
    return (
      <>
        <UserLogout>
          <BackgroundImgLogout>
            <ImgLogout src={backgroundImage} alt="" />
            <MessageLogout>
                !  !
              <ButtonLogout onClick={onClickHandler}>
                 
              </ButtonLogout>
            </MessageLogout>
          </BackgroundImgLogout>
        </UserLogout>
      </>
    );
  } else {
    return null;
  }
};

const UserLogout = styled.div`
  margin: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const BackgroundImgLogout = styled.div`
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const ImgLogout = styled.img`
  width: 560px;
  border-radius: 80px;
`;

const MessageLogout = styled.div`
  margin-top: 30px;
  width: 200px;
`;

const ButtonLogout = styled.button`
  margin: 20px;
  color: white;
  font-weight: 600;
  width: 200px;
  height: 80px;
  background-color: ${DEEP_BROWN};
  cursor: pointer;
  border: none;
  border-radius: 20px;
`;

export default Logout;
