import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";

const MyPageButton: React.FC = () => {
  const isToken = useSelector((state: any) => state.auth.isAuthenticated);
  return (
    <DivMainPost>
      {isToken ? (
        <>
          <LinkPage to="/logout">  </LinkPage>
          <LinkPage to="/mypage/0">  </LinkPage>
        </>
      ) : (
        <>
          <LinkPage to="/login">  </LinkPage>
          <LinkPage to="/signup">  </LinkPage>
          <LinkPage to="/find-email"> </LinkPage>
        </>
      )}
    </DivMainPost>
  );
};

const DivMainPost = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const LinkPage = styled(Link)`
  margin: 10px;
  color: black;

  cursor: pointer;
`;

export default MyPageButton;
